import { Component } from "@angular/core";
import { Storage } from "@ionic/storage";
import { NavController, NavParams, AlertController, LoadingController } from "ionic-angular";
import { Media, MediaObject } from "@ionic-native/media";
import { Toast } from "@ionic-native/toast";
import { Dialogs } from "@ionic-native/dialogs";
import { Observable } from "rxjs/Rx";
import "rxjs/add/operator/startWith";
import { Mixpanel } from "@ionic-native/mixpanel";


@Component({
  selector: "page-player",
  templateUrl: "player.html"
})
export class PlayerPage {
  sermon: any;
  playing: boolean = true;
  audioProgress: string;
  audioDuration: string;
  secDuration: number;
  progress: number = 0;
  isDownloaded: boolean = false;
  isLoading: boolean = true;
  mediaFile: MediaObject;
  skipped: number = 0;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private media: Media,
    public storage: Storage,
    public alertCtrl: AlertController,
    private toast: Toast,
    public loadingCtrl: LoadingController,
    private dialogs: Dialogs,
    private mixpanel: Mixpanel
  ) {
    this.sermon = navParams.get("sermon");
    this.storage.get("downloads").then(downloads => {
      if (downloads !== null) {
        downloads.forEach(element => {
          if (element.title.toLowerCase() === this.sermon.title.toLowerCase()) {
            this.isDownloaded = true;
            this.mediaFile = this.media.create(element.audio);
          }
        });
        if (this.isDownloaded === false) {
          this.mediaFile = this.media.create(this.sermon.audio);
        }
      } else {
        this.mediaFile = this.media.create(this.sermon.audio);
      }
      this.mediaFile.play();
      this.mediaFile.onStatusUpdate.subscribe(status => {
        if (status === 2) {
          this.isLoading = false;
        }
        if (status === 1) {
          this.sendMixPanel();
          const observe = Observable.interval(1000).subscribe(x => {
            const info = this.mediaFile.getDuration();
            if (info > 1) {
              observe.unsubscribe();
              this.secDuration = info;
              const min = Math.floor(info / 60);
              const sec = Math.floor(info - min * 60);
              this.audioDuration = min + ":" + sec;
            }
          });
        }
      });
    });
    const mediaPos = Observable.interval(1000).subscribe(x => {
      this.mediaFile.getCurrentPosition().then(position => {
        console.log("position", position);
        if (position > 1) {
          const min = Math.floor(position / 60);
          this.progress = Math.floor((position / this.secDuration) * 100);
          const sec = Math.floor(position % 60);
          this.audioProgress = min + ":" + (sec < 10 ? "0" + sec : sec);
          if (min === this.secDuration / 60) {
            mediaPos.unsubscribe();
          }
        }
      });
    });
  }

  sendMixPanel = () => {
    this.mixpanel.track("Played", {
      title: this.sermon.title
    });
  };

  ionViewCanLeave(): boolean {
    this.mediaFile.stop();
    this.mediaFile.release();
    return true;
  }

  playMedia = () => {
    this.mediaFile.play();
    this.playing = !this.playing;
  };

  pauseMedia = () => {
    this.mediaFile.pause();
    this.playing = !this.playing;
  };

  skipForward() {
    this.mediaFile.getCurrentPosition().then(position => {
      let mediaPosition = position + 15;
      const mediaInMilli = mediaPosition * 1000;
      this.mediaFile.seekTo(mediaInMilli);
    });
  }

  skipBackward() {
    this.mediaFile.getCurrentPosition().then(position => {
      let mediaPosition = position - 15;
      const mediaInMilli = mediaPosition * 1000;
      this.mediaFile.seekTo(mediaInMilli);
    });
  }
}



